import React, {Component} from 'react';
import VideoInteractive from './VideoInteractive';

class App extends Component {
   constructor() {
      super();
      this.state = {};
   }
   render() {
      let primary = '#4499cc';
      let secondary = '#557722';
      const disabled = '#D3D3D3';
      let dislabel = '#aaccdd';
      return (
      <div>
      <VideoInteractive primcolor={primary} seccolor={secondary} discolor={disabled} dislabel={dislabel} />
      </div>
      );
   }
}

export default App;
