import React, {Component} from 'react';
import stepData from './stepdata';
import StepCard from './StepCard';
import Reflect from './Reflect';
import InteractiveSVG from './InteractiveSVG';
import $ from 'jquery';

class VideoInteractive extends Component {
   constructor() {
      super();
      this.state = {
      currentButton : 1, mode : 'test', currentAnswerMark : 1, mediaDimTag : '', vidTimes : null,
      testKey : 1, audioVol : 1, mediaDims : '54vh', userTested : false, PNUM : 3
      };
      this.childClick = this.childClick.bind(this);
      this.startOver = this.startOver.bind(this);
      this.reset = this.reset.bind(this);
      this.upOne = this.upOne.bind(this);
      this.testMode = this.testMode.bind(this);
      this.learnMode = this.learnMode.bind(this);
      this.correctAnswer = this.correctAnswer.bind(this);
      this.updateTest = this.updateTest.bind(this);
      this.setAudio = this.setAudio.bind(this);
      this.vidListener = null;
      this.repeatStep = this.repeatStep.bind(this);
      this.updateState = this.updateState.bind(this);
   }
   userTested(res) {
      if (res) this.setState({userTested : true});
   }
   updateMedia() {
     let wdt = $('#media-container').width();
     let hgt = $('#media-container').height();
     let mediatag;
     if (wdt / hgt <= 1.2) mediatag = '_square';
     else mediatag = '';
     let vidTimes = stepData.map(function(item) {return item.timestamps;});
     this.setState({mediaDimTag : mediatag, vidTimes : vidTimes});
     if (wdt / hgt > 1.76) this.setState({mediaDims : {height : '65vh'}});
     else this.setState({mediaDims : {height : '54vh'}});
   }
   componentDidMount() {
      this.updateMedia();
      let wdt = $('#media-container').width();
      let hgt = $('#media-container').height();
      if (wdt / hgt > 1.76) this.setState({mediaDims : '65vh'});
      window.addEventListener('resize', this.updateMedia.bind(this));
   }
   setAudio() {
      let level = this.state.audioVol;
      if (level === 0) level = 1;
      else level = 0;
      this.setState({audioVol : level});
      let vid = document.getElementById('step-video');
      vid.volume = level;
   }
   childClick() {
      $('#vidProgress').css({width : 0});
      this.setState({currentButton : this.state.currentButton + 1}, function() {
      this.playVideo();
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
      setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 100);
      });
   }
   playVideo() {
      let vid = document.getElementById('step-video');
      vid.removeEventListener('timeupdate', this.vidListener);
      let segStart = this.state.vidTimes[this.state.currentButton - 2][0] || 0;
      let segEnd = this.state.vidTimes[this.state.currentButton - 2][1] || 0;
      vid.pause();
      vid.currentTime = segStart;
      vid.play();
      this.vidListener = function() {
         let segLn = ((vid.currentTime - segStart) / (segEnd - segStart)) * $('#media-container').width();
         $('#vidProgress').css({width : segLn});
         if (vid.currentTime >= segEnd) this.pause();
      }
      vid.addEventListener('timeupdate', this.vidListener);
   }
   correctAnswer(fin) {
      this.setState({currentAnswerMark : this.state.currentAnswerMark + 1});
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      if (!fin) {
         const cardLoc = document.getElementById(`card${this.state.currentAnswerMark}`).offsetTop;
         setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 800);
      }
   }
   startOver() {
      $('#vidProgress').css({width : 0});
      this.setState({currentButton : 2}, function(){
         if (this.state.mode === 'learn' && this.state.currentButton >= 2) this.playVideo();
      });
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
   }
   reset() {
      $('#vidProgress').css({width : 0});
      this.setState({currentButton : 1});
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
      let vid = document.getElementById('step-video');
      vid.removeEventListener('timeupdate', this.vidListener);
      vid.pause();
      vid.currentTime = 0;
   }
   upOne(evt) {
      evt.stopPropagation();
      $('#vidProgress').css({width : 0});
      this.setState({currentButton : this.state.currentButton - 1}, function(){
         if (this.state.currentButton === 1) {this.reset();} else {this.playVideo();}
         if (this.state.currentButton > 1) {
         const stepWindow = document.getElementById('steps-container');
         const stepWindowLoc = document.getElementById('steps-container').offsetTop;
         const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
         setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 100);
         }
      });
   }
   repeatStep(evt) {
      evt.stopPropagation();
      $('#vidProgress').css({width : 0});
      this.playVideo();
   }
   testMode() {
      let vid = document.getElementById('step-video');
      if (vid) vid.removeEventListener('timeupdate', this.vidListener);
      $('#vidProgress').css({width : '0px'});
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   updateTest() {
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1,
                     testKey : this.state.testKey + 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#fracInput').html('');
   }
   learnMode() {
      this.setState({currentButton : 1, mode : 'learn', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   updateState(num, ans) {
      this.setState({testKey : num, currentAnswerMark : ans, mode : 'test'});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      if (ans <= 5) {
      const cardLoc = document.getElementById(`card${ans}`).offsetTop;
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      setTimeout(function(){
      stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });
      }, 800);
      }
   }
   render() {
      let modeMedia, butStyle, buttonChoices, mediaStyle, reflect, audvis, repeat;
      let stepCards = [];
      let vidTimes = [];
      mediaStyle = this.state.mediaDims;

      mediaStyle = {height : this.state.mediaDims, background : 'radial-gradient(white, #fafafa)'};
      if (this.state.currentButton <= stepData.length) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};

      if (this.state.mode === 'learn') {
      for (let a = 0; a < stepData.length; a++) {
         stepCards.push(<StepCard key={stepData[a].id} label={stepData[a].label} text={stepData[a].text} order={stepData[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} upone={this.upOne} repstep={this.repeatStep} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} />);
         vidTimes.push(stepData[a].timestamps);
      }

      buttonChoices = (<span><button onClick={this.startOver} className='uk-button uk-button-default uk-button-small uk-width-1-3' style={butStyle}>redo</button><button style={{backgroundColor : this.props.seccolor}} onClick={this.reset} className='uk-button uk-button-secondary uk-button-small uk-width-1-3'>clear</button><button style={{backgroundColor : this.props.primcolor}} onClick={this.testMode} className='uk-button uk-button-primary uk-button-small uk-width-1-3'>show</button></span>);
      modeMedia = <video className='uk-height-1-1' id='step-video' src={this.state.mediaDimTag === '' ? 'fracallsizes_rectangle.mp4' : 'fracallsizes_square.mp4'} style={{objectFit : 'cover'}} />;
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
         audvis = 'hidden';
      }
      else audvis = 'visible';

      reflect = <Reflect suganswer='First, determine the number of equal parts in one whole thing. That is the denominator, or bottom number. Then, count the number of equal parts that are counted or shaded. That is the numerator, or top number.' reflectQ='How can you write a fraction from looking at a model?' />;
      }
      else  {
      if (this.state.testKey >= 3) repeat = 'again';
      else repeat = 'another one';
      let test;
      if (this.state.testKey % 3 === 1) test = 'test';
      else if (this.state.testKey % 3 === 2) test = 'test2';
      else test = 'test3';
      if (this.state.currentButton <= stepData.length) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};
      buttonChoices = (<span><button id='updateTest' style={{backgroundColor : this.props.seccolor}} onClick={this.updateTest} className='uk-button uk-button-secondary uk-button-small uk-width-1-2'>{repeat}</button><button style={{backgroundColor : this.props.primcolor}} onClick={this.learnMode} className='uk-button uk-button-primary uk-button-small uk-width-1-2'>learn&thinsp;<span uk-icon='social'></span></button></span>);
      for (let a = 0; a < stepData.length; a++) {
         stepCards.push(<StepCard key={stepData[a].id} label={stepData[a].testLabel} text={stepData[a][test]} order={stepData[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} curmark={this.state.currentAnswerMark} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} />)
      }

      modeMedia = <InteractiveSVG testnum={this.state.testKey} curans={this.state.currentAnswerMark} correctans={this.correctAnswer} tested={this.userTested} pnum={this.state.PNUM} updatestate = {this.updateState} />;

      reflect = null;
      audvis = 'hidden';
      }
      return (
      <div>
      <div id='media-container' className='uk-container uk-width-1-2@m uk-border-rounded' style={mediaStyle}>
      {modeMedia}
      <div id='vidProgress'></div>
      <img onClick={this.setAudio} id='soundicon' src='audio_icon_white.svg' alt='' style={{opacity : ((1 - this.state.audioVol) * 1.5) / 1.5 + 0.2, visibility : audvis}}/>
      </div>

      <div id='steps-container' className='uk-container uk-width-1-2@m'>
      {stepCards}
      {reflect}
      {buttonChoices}
      </div>
      </div>
      );
   }
}

export default VideoInteractive;
